<template>
  <div class="container">
    <br>
    <Row>
      <Col :xs="{push: 1}" :lg="{push: 0}">
        <Breadcrumb>
          <BreadcrumbItem :to="{name: 'home'}">{{ $t("header.index") }}</BreadcrumbItem>
          <BreadcrumbItem>{{ $t("header.profile") }}</BreadcrumbItem>
        </Breadcrumb>
      </Col>
    </Row>
    <br>

    <Card dis-hover>
      <Row>
        <Col flex="auto">
          <Row :gutter="15">
            <Col>
              <Avatar :size="48">{{ currentUser.userinfo.username[0] }}</Avatar>
            </Col>
            <Col flex="1">
              <h3>
                {{ currentUser.userinfo.username }}
              </h3>
              <p>{{ $t('profile.meet', {name: currentUser.userinfo.username}) }}</p>
            </Col>
            <Col v-if="currentUser.userinfo.privilege">
              <Form label-position="top">
                <FormItem :label="$t('profile.account.form.privilege')">
                <span v-for="(privileges_item, privileges_index) in privileges" :key="privileges_index">
                  <span v-for="(p, pi) in currentUser.userinfo.privilege" :key="pi">
                    <Tag type="border" :color="privileges_item.class" v-if="p == privileges_item.value">
                      {{ $t('basic.privilege.' + p) }}
                    </Tag>
                  </span>
                </span>
                </FormItem>
              </Form>
            </Col>
          </Row>
        </Col>
        <Col>

        </Col>
      </Row>
      <br>
      <Row :gutter="30">
        <Col span="6">
          <List border size="small">
            <ListItem v-for="(i, index) in muen" :key="i.value">
              <a @click="upDateUri(muen[index].value)">
                <b v-if="muen[index].value == muenIndex">
                  {{ $t("profile." + muen[i.value].title + ".title") }}
                </b>
                <span v-else>{{ $t("profile." + muen[i.value].title + ".title") }}</span>
              </a>
            </ListItem>
          </List>
          <br>
          <List border size="small">
            <ListItem>
              <router-link :to="{name: 'account', params: { uId: `${currentUser.userinfo.userId }` }}">
                {{ $t("header.userCenter") }}
                <Icon type="ios-link"/>
              </router-link>
            </ListItem>
          </List>
          <br>

          <Divider>{{ $t("home.bulletin.title") }}</Divider>
          <Bulletin/>
        </Col>
        <Col span="18">
          <account v-if="muenIndex == 0"></account>
          <appearance v-if="muenIndex == 1"></appearance>
          <message v-if="muenIndex == 2"></message>
          <enhance v-if="muenIndex == 3"></enhance>
          <media v-if="muenIndex == 4"></media>
        </Col>
      </Row>
    </Card>
    <br>
  </div>
</template>

<script>
import Bulletin from "../../components/Bulletin";

import appearance from "./appearance";
import account from "./account";
import message from "./message";
import enhance from "./enhance";
import media from "./media";

export default {
  name: "profile",
  data() {
    return {
      privileges: [],
      muenIndex: 0,
      muen: [{
          title: 'account',
          value: '0'
        }, {
          title: 'appearance',
          value: '1'
        },
        {
          title: 'message',
          value: '2'
        },
        {
          title: 'enhance',
          value: '3'
        },
        {
          title: 'media',
          value: '4'
        },
      ]
    }
  },
  components: {Bulletin, appearance, account, message, enhance, media},
  created() {
    const pagename = this.$route.params.pagename;

    if (pagename == undefined) {
      this.upDateUri(0, true);
      return;
    }

    let name = this.muen.filter(
        i => {
          return i.title.toLocaleLowerCase() == pagename;
        }
    )[0].value;

    this.upDateUri(name || this.muenIndex);
    this.loadData();
  },
  methods: {
    async loadData() {
      const privileges = await import('/public/conf/privilege.json');
      this.privileges = this.privileges.concat(privileges.child);
    },
    upDateUri(index, t = false) {
      this.muenIndex = index;
      this.$router.push({
        path: (t ? 'profile/' : '') + this.muen[index].title.toLowerCase()
      });
    }
  },
  computed: {
    isLogin() {
      return Boolean(this.$store.state.user);
    },
    isAdmin() {
      const user = this.$store.state.user;
      const is = user ? user.userinfo.privilege.concat("").includes("admin") : false;
      return Boolean(is);
    },
    currentUser() {
      return this.$store.state.user;
    }
  }
}
</script>

